import ___CSS_LOADER_API_IMPORT___ from "../../../../node_modules/css-loader/dist/runtime/api.js";
var ___CSS_LOADER_EXPORT___ = ___CSS_LOADER_API_IMPORT___(___CSS_LOADER_API_SOURCEMAP_IMPORT___);
// Module
___CSS_LOADER_EXPORT___.push([module.id, ".table thead th,table tr>td,table tr>th{vertical-align:middle}.table thead th{background:gray;border:1px solid #fff;padding:3px;text-align:center}.table td{cursor:move}.table td,.table th{padding:3px;vertical-align:middle}.table td input,.table th input{padding:3px}.calculator{color:#000}table.table-striped{border:3px solid gray}table.table-striped tr.calculator{background-color:#fff;border-bottom:1 solid #000;border-top:1 solid #000}table{border-collapse:collapse;width:100%}.select2-container{width:100%!important}.select2-container .select2-selection--single{border:1px solid #ced4da!important;border-radius:.25rem!important;height:38px!important}.select2-container .select2-selection--single .select2-selection__rendered{color:#495057!important;line-height:36px!important;padding-left:12px!important}.select2-container .select2-selection--single .select2-selection__arrow{height:36px!important;right:10px!important}.select2-container--default .select2-selection--single .select2-selection__placeholder{color:#6c757d!important}.select2-dropdown{border:1px solid #ced4da!important;border-radius:.25rem!important}", "",{"version":3,"sources":["webpack://./resources/js/app/pages/Invoice-Edit.vue"],"names":[],"mappings":"AAglCA,wCAHI,qBASJ,CANA,gBAGI,eAAgB,CAChB,qBAAuB,CAFvB,WAAY,CAGZ,iBACJ,CAEA,UACI,WACJ,CAEA,oBAGI,WAAY,CADZ,qBAEJ,CAEA,gCAEI,WACJ,CAEA,YACI,UACJ,CAEA,oBAII,qBACJ,CAEA,kCACI,qBAAsB,CAItB,0BAA0B,CAI1B,uBACJ,CAEA,MACI,wBAAyB,CACzB,UACJ,CAGA,mBACI,oBACJ,CAEA,8CAGI,kCAAoC,CADpC,8BAAiC,CADjC,qBAGJ,CAEA,2EAGI,uBAAyB,CAFzB,0BAA4B,CAC5B,2BAEJ,CAEA,wEACI,qBAAuB,CACvB,oBACJ,CAEA,uFAGI,uBACJ,CAEA,kBAEI,kCAAoC,CADpC,8BAEJ","sourcesContent":["<template>\n    <div class=\"container-fluid\">\n        <div class=\"row\">\n            <div class=\"col-12\">\n                <!-- Main content -->\n                <div class=\"invoice p-3 mb-3\">\n                    <div style=\"\">\n                        <div class=\"form-group row\">\n                            <label\n                                for=\"staticEmail\"\n                                class=\"col-sm-3 col-form-label\"\n                                ><b>:</b></label\n                            >\n                            <div class=\"col-sm-9\">\n                                <input\n                                    class=\"form-control\"\n                                    type=\"text\"\n                                    v-model=\"v$.model.$model.title\"\n                                />\n                            </div>\n                        </div>\n                    </div>\n                    <hr />\n\n                    <p class=\"lead\"></p>\n                    <div class=\"row invoice-info\">\n                        <div class=\"col-sm-8 invoice-col\">\n                            <address>\n                                <input type=\"hidden\" v-model=\"m.purchaser\" />\n\n                                <div\n                                    class=\"row mb-2\"\n                                    style=\"align-items: center\"\n                                >\n                                    <div class=\"col-4\">\n                                        <b>  :</b>\n                                    </div>\n                                    <div class=\"col-8\">\n                                        <input\n                                            class=\"form-control\"\n                                            type=\"text\"\n                                            v-model=\"\n                                                v$.model.purchaser.$model.name\n                                            \"\n                                        />\n                                    </div>\n                                </div>\n\n                                <div\n                                    class=\"row mb-2\"\n                                    style=\"align-items: center\"\n                                >\n                                    <div class=\"col-4\">\n                                        <b>  :</b>\n                                    </div>\n                                    <div class=\"col-8\">\n                                        <input\n                                            class=\"form-control\"\n                                            type=\"text\"\n                                            v-model=\"\n                                                v$.model.purchaser.$model\n                                                    .subj_name\n                                            \"\n                                        />\n                                    </div>\n                                </div>\n\n                                <div\n                                    class=\"row mb-2\"\n                                    style=\"align-items: center\"\n                                >\n                                    <div class=\"col-4\">\n                                        <b>  :</b>\n                                    </div>\n                                    <div class=\"col-8\">\n                                        <input\n                                            class=\"form-control\"\n                                            type=\"text\"\n                                            v-model=\"\n                                                v$.model.purchaser.$model\n                                                    .subj_address\n                                            \"\n                                        />\n                                    </div>\n                                </div>\n\n                                <div\n                                    class=\"row mb-2\"\n                                    style=\"align-items: center\"\n                                >\n                                    <div class=\"col-4\">\n                                        <b>  :</b>\n                                    </div>\n                                    <div class=\"col-8\">\n                                        <input\n                                            class=\"form-control\"\n                                            type=\"text\"\n                                            v-model=\"\n                                                v$.model.purchaser.$model\n                                                    .identification_num\n                                            \"\n                                        />\n                                    </div>\n                                </div>\n\n                                <!-- Phone: (555) 539-1037<br>\n                    Email: john.doe@example.com -->\n                            </address>\n                        </div>\n                        <!-- /.col -->\n                        <div\n                            class=\"col-sm-4 invoice-col\"\n                            style=\"\n                                align-items: flex-end;\n                                display: flex;\n                                flex-direction: column;\n                                text-align: right;\n                            \"\n                        >\n                            <div>\n                                <b>Invoice {{ model.uuid }}</b\n                                ><br />\n                            </div>\n                            <div v-if=\"model.parent\">\n                                <b>:</b> {{ model.parent.uuid }}\n                            </div>\n                        </div>\n                        <!-- /.col -->\n                    </div>\n                    <button\n                        :disabled=\"m.purchaser.id && model.id\"\n                        type=\"button\"\n                        @click=\"setter('purchasers')\"\n                        class=\"btn btn-sm btn-outline-success\"\n                    >\n                        <i class=\"fas fa-shield-alt\"></i> \n                    </button>\n                    <!-- /.row -->\n\n                    <!-- Response and Repair Assignment Section -->\n                    <hr />\n                    <p class=\"lead\">  ()</p>\n                    <div class=\"row\">\n                        <div class=\"col-md-6\">\n                            <div class=\"form-group\">\n                                <label for=\"response_select\"\n                                    ><b>:</b></label\n                                >\n                                <select\n                                    id=\"response_select\"\n                                    class=\"form-control select2\"\n                                    v-model=\"v$.model.$model.response_id\"\n                                >\n                                    <option value=\"\"> </option>\n                                    <option\n                                        v-for=\"response in additional.responses\"\n                                        :key=\"response.id\"\n                                        :value=\"response.id\"\n                                    >\n                                        #QR{{ response.id }} -\n                                        {{ response.name }} ({{\n                                            response.subject_name\n                                        }})\n                                    </option>\n                                </select>\n                            </div>\n                        </div>\n                        <div class=\"col-md-6\">\n                            <div class=\"form-group\">\n                                <label for=\"repair_select\"\n                                    ><b>:</b></label\n                                >\n                                <select\n                                    id=\"repair_select\"\n                                    class=\"form-control select2\"\n                                    v-model=\"v$.model.$model.repair_id\"\n                                >\n                                    <option value=\"\"> </option>\n                                    <option\n                                        v-for=\"repair in additional.repairs\"\n                                        :key=\"repair.id\"\n                                        :value=\"repair.id\"\n                                    >\n                                        #RP{{ repair.id }} -\n                                        {{ repair.name }} ({{\n                                            repair.subject_name\n                                        }})\n                                    </option>\n                                </select>\n                            </div>\n                        </div>\n                    </div>\n\n                    <div class=\"row\">\n                        <div\n                            class=\"col text-end d-flex align-items-center justify-content-end\"\n                        >\n                            <label for=\"count\" class=\"mr-2\"\n                                > :\n                            </label>\n                            <input\n                                type=\"number\"\n                                id=\"count\"\n                                class=\"form-control form-control-sm w-auto\"\n                                v-model=\"v$.model.$model.warranty_period\"\n                                style=\"max-width: 80px\"\n                            />\n                        </div>\n                    </div>\n\n                    <hr />\n\n                    <!-- Table row -->\n                    <div class=\"row\">\n                        <div class=\"col-12 table-responsive\">\n                            <p class=\"lead\"></p>\n\n                            <table\n                                class=\"table table-striped\"\n                                style=\"background: #edebe4; color: #fff\"\n                            >\n                                <thead>\n                                    <tr>\n                                        <th style=\"width: 30%\"></th>\n                                        <th style=\"width: 20%\"></th>\n                                        <th></th>\n                                        <th></th>\n                                        <th style=\"text-align: center\">\n                                             \n                                        </th>\n                                        <th style=\"text-align: center\">\n                                             \n                                        </th>\n                                        <th></th>\n                                        <th></th>\n                                    </tr>\n                                </thead>\n\n                                <draggable\n                                    v-model=\"m.category_attributes\"\n                                    tag=\"tbody\"\n                                    item-key=\"id\"\n                                    :disabled=\"step\"\n                                >\n                                    <template #item=\"{ element }\">\n                                        <request-single-attribute\n                                            @action_focus=\"try_focus\"\n                                            @action_blur=\"try_blur\"\n                                            @action_price=\"price\"\n                                            @action_remove=\"remove\"\n                                            :keys=\"keys\"\n                                            :join-in-tree=\"\n                                                attributeJoinedTree(element)\n                                            \"\n                                            :item=\"element\"\n                                            :model=\"m\"\n                                        />\n                                    </template>\n                                </draggable>\n\n                                <tbody>\n                                    <tr class=\"calculator\">\n                                        <th colspan=\"4\"> :</th>\n                                        <th>\n                                            <input\n                                                class=\"form-control\"\n                                                type=\"text\"\n                                                readonly\n                                                v-model=\"agr.price\"\n                                            />\n                                        </th>\n                                        <td>\n                                            <input\n                                                class=\"form-control\"\n                                                type=\"text\"\n                                                readonly\n                                                v-model=\"agr.service_price\"\n                                            />\n                                        </td>\n                                        <td>\n                                            <input\n                                                class=\"form-control\"\n                                                type=\"text\"\n                                                readonly\n                                                v-model=\"agr.calc\"\n                                            />\n                                        </td>\n                                        <td></td>\n                                    </tr>\n                                </tbody>\n                            </table>\n\n                            <button\n                                type=\"button\"\n                                @click=\"setter('categories')\"\n                                class=\"btn btn-sm btn-outline-success mr-1\"\n                            >\n                                <i class=\"fas fa-shield-alt\"></i> \n                            </button>\n\n                            <button\n                                type=\"button\"\n                                @click=\"findSpecialAtribute('new')\"\n                                class=\"btn btn-sm btn-outline-warning\"\n                            >\n                                <i class=\"fas fa-shield-alt\"></i> \n                            </button>\n                        </div>\n                        <!-- /.col -->\n                    </div>\n                    <!-- /.row -->\n                    <hr />\n\n                    <!-- /.row -->\n                    <hr />\n\n                    <!-- this row will not appear when printing -->\n                    <div class=\"row no-print\">\n                        <div class=\"col-12\">\n                            <a rel=\"\" @click=\"exit\" class=\"btn btn-warning mr-1\"\n                                ></a\n                            >\n                            <a\n                                rel=\"\"\n                                target=\"_blank\"\n                                @click=\"(e) => remove(e, model, true)\"\n                                class=\"btn btn-danger\"\n                                ></a\n                            >\n\n                            <button\n                                :disabled=\"v$.m.$invalid || v$.model.$invalid\"\n                                @click=\"send\"\n                                type=\"button\"\n                                class=\"btn btn-success float-right\"\n                            >\n                                <i class=\"far fa-credit-card\"></i> \n                            </button>\n                            <button\n                                :disabled=\"v$.m.$invalid || v$.model.$invalid\"\n                                @click=\"(e) => gadawera(e, model, true)\"\n                                type=\"button\"\n                                class=\"btn btn-primary float-right\"\n                                style=\"margin-right: 5px\"\n                            >\n                                <i class=\"far fa-copy\"></i> \n                            </button>\n                        </div>\n                    </div>\n\n                    <hr />\n\n                    <h3></h3>\n                    <hr />\n                    <div class=\"overflow-x-auto\">\n                        <table\n                            class=\"table-auto border-collapse border border-gray-400 w-full\"\n                        >\n                            <thead>\n                                <tr class=\"bg-gray-200\">\n                                    <th class=\"border border-gray-400 p-2\">\n                                        \n                                    </th>\n                                    <th class=\"border border-gray-400 p-2\">\n                                          \n                                    </th>\n                                    <th class=\"border border-gray-400 p-2\">\n                                        \n                                    </th>\n                                    <th class=\"border border-gray-400 p-2\">\n                                         \n                                    </th>\n                                    <th class=\"border border-gray-400 p-2\">\n                                          ( 7%)\n                                    </th>\n                                    <th class=\"border border-gray-400 p-2\">\n                                         \n                                    </th>\n                                    <th class=\"border border-gray-400 p-2\">\n                                          \n                                    </th>\n                                </tr>\n                            </thead>\n                            <tbody>\n                                <tr>\n                                    <td>\n                                        <select\n                                            v-model=\"calculatorType\"\n                                            id=\"calculatorType\"\n                                            class=\"form-control\"\n                                        >\n                                            <option value=\"0\">\n                                                   \n                                                -  ()\n                                            </option>\n                                            <option value=\"1\">\n                                                   \n                                                 -  ()\n                                            </option>\n                                            <option value=\"2\">\n                                                   \n                                                  - \n                                                (. )\n                                            </option>\n                                        </select>\n                                    </td>\n                                    <td\n                                        class=\"border border-gray-400 p-2 bg-gray-300\"\n                                    >\n                                        <input\n                                            type=\"number\"\n                                            v-model.number=\"minPayment\"\n                                            class=\"w-full text-center\"\n                                        />\n                                    </td>\n                                    <td\n                                        class=\"border border-gray-400 p-2 bg-gray-300\"\n                                    >\n                                        {{ priceIncrease }}\n                                    </td>\n                                    <td class=\"border border-gray-400 p-2\">\n                                        {{ sellSum }}\n                                    </td>\n                                    <td class=\"border border-gray-400 p-2\">\n                                        <input\n                                            type=\"number\"\n                                            v-model.number=\"discountPercentage\"\n                                            @input=\"validateDiscount\"\n                                            class=\"w-full text-center\"\n                                        />\n                                    </td>\n                                    <td class=\"border border-gray-400 p-2\">\n                                        {{ discountAmount }}\n                                    </td>\n                                    <td class=\"border border-gray-400 p-2\">\n                                        {{ finalPrice }} \n                                    </td>\n                                </tr>\n                            </tbody>\n                        </table>\n                    </div>\n                </div>\n                <!-- /.invoice -->\n            </div>\n            <!-- /.col -->\n        </div>\n        <!-- /.row -->\n\n        <modal\n            v-if=\"showModal\"\n            @exit=\"try_exit\"\n            :categories=\"selectBuilder\"\n            :selector=\"selector\"\n            :setting=\"setting\"\n        ></modal>\n    </div>\n</template>\n\n<script>\nimport Util from \"Util\";\nimport useVuelidate from \"@vuelidate/core\";\nimport { required, maxLength } from \"@vuelidate/validators\";\nimport draggable from \"../vendors/vuedraggable/src/vuedraggable\";\nimport \"select2/dist/css/select2.css\";\nimport \"select2\";\n\nexport default {\n    props: [\"user\", \"model\", \"setting\", \"additional\"],\n    components: {\n        draggable,\n    },\n    setup(props) {\n        return { v$: useVuelidate() };\n    },\n    created() {\n        this.m = this.attributeInit;\n\n        // console.log('this.additional', this.additional)\n\n        // console.log('test', this.recurcive(this.test, 3))\n        // console.log('p1: 0, p2: 0', this.calculate)\n        // this.c = this.calculate\n        // console.log('this.c', this.c)\n\n        // let initReporteValue = Util.initReporteValues(this.names, this.m);\n\n        // console.log('test', initReporteValue)\n        // console.log('test2', Util.itemReportValue(30, this.m, initReporteValue))\n\n        this.calculator.percenters = this.percenters;\n        this.calculator.inputs = this.initReporteValues;\n\n        this.keys = [\"price\", \"service_price\", \"calc\"];\n\n        if (!this.model.purchaser)\n            this.model.purchaser = {\n                name: \"\",\n                subj_name: \"\",\n                subj_address: \"\",\n                identification_num: \"\",\n            };\n\n        // Initialize response_id and repair_id if not set\n        if (this.model.response_id === undefined) {\n            this.model.response_id = \"\";\n        }\n        if (this.model.repair_id === undefined) {\n            this.model.repair_id = \"\";\n        }\n    },\n    mounted() {\n        if (this.m.category_attributes.length && Util._isEmpty(this.agr)) {\n            this.agr = Util.agr(this.m.category_attributes);\n        }\n        this.v$.model.$touch();\n        this.v$.m.$touch();\n\n        // Initialize Select2 for response and repair selectors\n        this.$nextTick(() => {\n            this.initializeSelect2();\n        });\n    },\n    data() {\n        return {\n            num1: 0,\n            num2: 0,\n            result: null,\n            showModal: false,\n            selector: \"\",\n            step: false,\n            minPayment: 0,\n            markup: 0,\n            discountPercentage: 0,\n            calculatorType: \"0\",\n            agr: {},\n            m: {},\n            c: [],\n\n            calculator: {\n                names: [\n                    \"   :\",\n                    \"  :\",\n                    \" :\",\n                    \"  :\",\n                    \" :\",\n                ],\n            },\n            keys: [],\n\n            selectBuilder: [],\n        };\n    },\n    watch: {\n        \"m.category_attributes\": {\n            handler(newValue, prevValue) {\n                if (newValue && newValue.length && prevValue) {\n                    this.agr = Util.agr(newValue, true);\n                }\n            },\n            deep: true,\n            immediate: true,\n        },\n        agr: {\n            handler(newValue, prevValue) {\n                if (!Util._isEmpty(newValue) && prevValue) {\n                    this.recurcive(this.calculator.inputs, newValue.calc);\n                }\n            },\n            deep: true,\n            immediate: true,\n        },\n        \"calculator.inputs\": {\n            handler(newValue, prevValue) {\n                if (newValue && prevValue && newValue.length) {\n                    newValue.map((i) => {\n                        this.m[i.inputName] = i.value;\n                    });\n\n                    this.m.category_attributes.map((attribute) => {\n                        this.keys.map((key) => {\n                            if (attribute.pivot[key]) {\n                                attribute.pivot[key] = Util.itemReportValue(\n                                    attribute[key],\n                                    this.initReporteValues\n                                );\n                            }\n                        });\n                    });\n                }\n            },\n            deep: true,\n            immediate: true,\n        },\n    },\n    validations() {\n        let validateRule = {\n            m: {\n                category_attributes: { required },\n            },\n            model: {\n                title: { required },\n                warranty_period: { required },\n                purchaser: {\n                    name: { required },\n                    subj_name: { required },\n                    subj_address: { required },\n                    identification_num: { required },\n                },\n            },\n        };\n        return validateRule;\n    },\n    computed: {\n        finalPrice() {\n            return (\n                Number(this.sellSum) -\n                Number(this.sellSum * (this.discountPercentage / 100))\n            ).toFixed(2);\n        },\n        withAdditionalCosts() {\n            let extraPercentage = 0;\n\n            if (this.calculatorType === \"1\") {\n                extraPercentage = 18;\n            } else if (this.calculatorType === \"2\") {\n                extraPercentage = 47.5;\n            }\n\n            if (this.calculatorType === \"0\") {\n                return this.minPayment;\n            }\n\n            return this.minPayment + (this.minPayment * extraPercentage) / 100;\n        },\n\n        priceIncrease() {\n            return (\n                this.minPayment\n                    ? this.withAdditionalCosts *\n                      (this.additional.price_increase / 100)\n                    : 0\n            ).toFixed(2);\n        },\n        sellSum() {\n            return (\n                Number(this.withAdditionalCosts) + Number(this.priceIncrease)\n            ).toFixed(2);\n        },\n        discountAmount() {\n            return Number(\n                this.sellSum * (this.discountPercentage / 100)\n            ).toFixed(2);\n        },\n        attributeJoinedTree: (app) => (item) => {\n            return item.category_id\n                ? app.additional.categories.find(\n                      (i) => i.id == item.category_id\n                  ).category_attributes\n                : [];\n        },\n\n        initReporteValues() {\n            return Array.from(\n                { length: this.calculator.names.length },\n                (_, i) => {\n                    let title = this.calculator.names[i];\n                    let inputName = \"p\" + (i + 1);\n                    let value = this.m[inputName];\n\n                    return { title, inputName, value };\n                }\n            );\n        },\n\n        percenters() {\n            return Array.from(\n                { length: this.calculator.names.length },\n                (_, i) => {\n                    return { p1: 0, p2: 0 };\n                }\n            );\n        },\n\n        attributeInit() {\n            this.model.category_attributes =\n                this.model.category_attributes.reduce((acumu, res) => {\n                    let init = res;\n                    let hasSpecial = this.model.special.find(\n                        (s) => s.category_attribute_id === res.id\n                    );\n                    if (hasSpecial) {\n                        init.isSpecial = true;\n                    }\n                    if (hasSpecial && !res.pivot.is_special) {\n                        res.pivot.price = hasSpecial.json.price;\n                        res.pivot.is_special = true;\n                        res.pivot.service_price = hasSpecial.json.service_price;\n                        res.pivot.calc = this.specNum(\n                            res.pivot.qty *\n                                (parseFloat(res.pivot.price) +\n                                    parseFloat(res.pivot.service_price))\n                        );\n                        init = { ...hasSpecial.json, pivot: res.pivot };\n                    }\n                    (acumu || []).push(init);\n                    return acumu;\n                }, []);\n            return this.model;\n        },\n    },\n    methods: {\n        validateDiscount() {\n            const maxDiscount = this.calculatorType === \"0\" ? 7 : 15;\n            if (this.discountPercentage > maxDiscount) {\n                this.discountPercentage = maxDiscount;\n                alert(\n                    \"    \" + maxDiscount + \"% - \"\n                );\n            }\n        },\n        addNumbers() {\n            this.result = parseFloat(this.num1) + parseFloat(this.num2);\n        },\n        recurcive(arr, price, index = 0) {\n            if (arr[index] != undefined) {\n                this.calculator.percenters[index].p1 = this.specNum(\n                    (arr[index].value * price) / 100\n                );\n                this.calculator.percenters[index].p2 = this.specNum(\n                    price + this.calculator.percenters[index].p1\n                );\n\n                let nextPrice = this.calculator.percenters[index].p2;\n\n                index = index + 1;\n                return this.recurcive(arr, nextPrice, index);\n            } else {\n                return arr;\n            }\n        },\n\n        specNum(num) {\n            if (!num) return 0.0;\n            let number = Math.ceil(parseFloat(num) * 100) / 100;\n            // return parseFloat(new Intl.NumberFormat('en-Us', {\n            //   minimumFractionDigits: 2,\n            //   maximumFractionDigits: 2\n            // }).format(num));\n            return parseFloat(number);\n        },\n\n        price(event, price, alter = false) {\n            if (!event.target.value) {\n                price.pivot[event.target.getAttribute(\"name\")] = 0;\n            }\n            if (!alter) price.pivot.qty = Number(event.target.value);\n            price.pivot.calc = this.specNum(\n                price.pivot.qty *\n                    (parseFloat(price.pivot.price) +\n                        parseFloat(price.pivot.service_price))\n            );\n        },\n\n        removeRequest(model, setting, callback, isReserve = null) {\n            let isNew = isReserve ? model.id : model.pivot.id;\n            let invoice = this.m.id ? this.m.id : !!this.m.id;\n\n            if (!isNew) {\n                return callback();\n            }\n\n            let action = document\n                .querySelector(\"form#render\")\n                .getAttribute(\"action\");\n            let token = document\n                .querySelector('meta[name=\"csrf-token\"')\n                .getAttribute(\"content\");\n\n            return this.$http\n                .delete(\n                    `${setting.url.nested.destroy.replace(\"__id__\", isNew)}`,\n                    { data: { reserve: isReserve, invoice } },\n                    {\n                        \"Content-Type\": \"application/json\",\n                        \"X-CSRF-TOKEN\": token,\n                    }\n                )\n                .then(async (response) => {\n                    if (response.data.success == true) {\n                        callback();\n                    } else {\n                        response.data.errs.map((item) =>\n                            this.$toast.error(item, {\n                                position: \"top-right\",\n                                duration: 7000,\n                            })\n                        );\n                    }\n                })\n                .catch((e) => {\n                    if (e.response.statusText)\n                        this.$toast.error(e.response.statusText, {\n                            position: \"top-right\",\n                            duration: 7000,\n                        });\n                });\n        },\n\n        remove(e, model, isReserve) {\n            e.preventDefault();\n            return Util.useSwall2(model).then((result) => {\n                if (result.isConfirmed) {\n                    this.removeRequest(\n                        model || null,\n                        this.setting,\n                        () => {\n                            if (!isReserve) {\n                                let index =\n                                    this.m.category_attributes.findIndex(\n                                        (i) => i.uuid == model.uuid\n                                    );\n                                this.m.category_attributes.splice(index, 1);\n                            }\n\n                            Swal.fire(\n                                \"!\",\n                                \"  .\",\n                                \"success\"\n                            ).then(() => {\n                                if (isReserve) {\n                                    window.location.replace(\n                                        this.setting.url.request.index.replace(\n                                            \"api/\",\n                                            \"\"\n                                        )\n                                    );\n                                }\n                            });\n                        },\n                        isReserve\n                    );\n                }\n            });\n        },\n\n        findSpecialAtribute(res) {\n            let existInModel = this.m.category_attributes.find(\n                (i) => i.id == res.id\n            );\n            let existInSpeciail = this.m.special.find(\n                (i) => i.category_attribute_id == res.id\n            );\n            if (res == \"new\") {\n                // this.m.category_attributes.push({\n                //   category_id: null,\n                //   name: '',\n                //   pivot:[],\n                //   nested:[],\n                //   price:0,\n                //   service_price:0,\n                //   pivot: {\n                //     qty: 1,\n                //     price: '',\n                //     service_price: '',\n                //     calc:  '',\n                //     evaluation_calc:  ''\n                //   }\n                // })\n\n                this.m.category_attributes.push({\n                    category_id: null,\n                    name: \"\",\n                    pivot: [],\n                    nested: [],\n                    price: 0,\n                    item: \"\",\n                    uuid: Util.uuid(),\n                    parent_uuid: null,\n                    category_type: 0,\n                    service_price: 0,\n                    pivot: {\n                        qty: 1,\n                        price: \"\",\n                        service_price: \"\",\n                        calc: \"\",\n                        evaluation_calc: \"\",\n                    },\n                });\n            } else if (existInModel) {\n                existInModel.pivot.qty = existInModel.pivot.qty + 1;\n                // existInModel.pivot.price = existInModel.price * existInModel.pivot.qty;\n                existInModel.pivot.calc = this.specNum(\n                    existInModel.pivot.qty *\n                        (parseFloat(existInModel.pivot.price) +\n                            parseFloat(existInModel.pivot.service_price))\n                );\n                existInModel.pivot.evaluation_calc = Util.itemReportValue(\n                    existInModel.pivot.calc,\n                    this.initReporteValues\n                );\n            } else if (existInSpeciail) {\n                existInSpeciail = existInSpeciail.json;\n                existInSpeciail.pivot = {\n                    qty: 1,\n                    price: Util.itemReportValue(\n                        existInSpeciail.price,\n                        this.initReporteValues\n                    ),\n                    is_special: true,\n                    category_attribute_id: res.id,\n                    service_price: Util.itemReportValue(\n                        existInSpeciail.service_price,\n                        this.initReporteValues\n                    ),\n                    calc: this.specNum(\n                        existInSpeciail.service_price + existInSpeciail.price\n                    ),\n                    evaluation_calc: Util.itemReportValue(\n                        this.specNum(\n                            existInSpeciail.service_price +\n                                existInSpeciail.price\n                        ),\n                        this.initReporteValues\n                    ),\n                };\n                this.m.category_attributes.push(existInSpeciail);\n            } else {\n                this.m.category_attributes.push({\n                    ...res,\n                    pivot: {\n                        qty: 1,\n                        price: Util.itemReportValue(\n                            res.price,\n                            this.initReporteValues\n                        ),\n                        category_attribute_id: res.id,\n                        service_price: Util.itemReportValue(\n                            res.service_price,\n                            this.initReporteValues\n                        ),\n                        calc: this.specNum(res.service_price + res.price),\n                        evaluation_calc: Util.itemReportValue(\n                            this.specNum(res.service_price + res.price),\n                            this.initReporteValues\n                        ),\n                    },\n                });\n            }\n        },\n\n        try_exit(res, selector) {\n            this.showModal = false;\n\n            if (selector == \"purchasers\") {\n                this.model.purchaser = res;\n                this.model.special = res.special_attributes;\n            } else if (selector == \"categories\") {\n                this.findSpecialAtribute(res);\n            }\n        },\n\n        setter(name) {\n            this.selectBuilder = [{ selected: \"\", res: this.additional[name] }];\n            this.selector = name;\n            this.showModal = true;\n        },\n\n        send(e) {\n            e.preventDefault();\n\n            let action = document\n                .querySelector(\"form#render\")\n                .getAttribute(\"action\");\n            let token = document\n                .querySelector('meta[name=\"csrf-token\"')\n                .getAttribute(\"content\");\n\n            let filter = this.model.category_attributes\n                .map(({ nested, ...rest }) => rest)\n                .map((item, index) => {\n                    item.pivot.sort = index + 1;\n                    return item;\n                });\n            this.model.category_attributes = filter;\n\n            // this.model\n            //this.$http.post(action, this.model, {\"Content-Type\": \"application/json\",'X-CSRF-TOKEN': token })\n\n            this.$http\n                .post(action, this.model, {\n                    \"Content-Type\": \"application/json\",\n                    \"X-CSRF-TOKEN\": token,\n                })\n                .then(async (response) => {\n                    Util.useSwall(response).then((result) => {\n                        if (result.value) {\n                            window.location.replace(\n                                this.setting.url.request.index.replace(\n                                    \"api/\",\n                                    \"\"\n                                )\n                            );\n                        } else if (\n                            this.model.id == null &&\n                            response.data.success == true\n                        ) {\n                            window.location.replace(\n                                window.location.href.replace(\n                                    \"new\",\n                                    response.data.result.id\n                                )\n                            );\n                        }\n\n                        if (!response.data.success) {\n                            if (response.data.errs)\n                                response.data.errs.map((item) =>\n                                    this.$toast.error(item, {\n                                        position: \"top-right\",\n                                        duration: 7000,\n                                    })\n                                );\n                        }\n                    });\n                })\n                .catch((e) => {\n                    this.$toast.error(e.response.statusText, {\n                        position: \"top-right\",\n                        duration: 7000,\n                    });\n                });\n\n            return false;\n        },\n\n        gadawera(e, model, isReserve) {\n            let session = [{ ...this.model }]\n                .map(({ purchaser, purchaser_id, special, ...rest }) => rest)\n                .find((_, index) => index == 0);\n\n            delete session.id;\n            session.special = [];\n            session.purchaser = {};\n            session.parent_uuid = model.uuid;\n            session.category_attributes.map((i) => {\n                delete i.pivot.id;\n                delete i.pivot.attributable_id;\n                return i;\n            });\n\n            sessionStorage.setItem(\"model\", JSON.stringify(session));\n            window.location.href = this.setting.url.request.edit;\n        },\n\n        try_focus() {\n            this.step = true;\n        },\n\n        try_blur() {\n            this.step = false;\n        },\n\n        exit() {\n            return (window.location.href =\n                this.setting.url.request.index.replace(\"api/\", \"\"));\n        },\n\n        initializeSelect2() {\n            // Initialize Select2 for response selector\n            if ($(\"#response_select\").length) {\n                $(\"#response_select\")\n                    .select2({\n                        placeholder: \" \",\n                        allowClear: true,\n                        width: \"100%\",\n                    })\n                    .on(\"change\", (e) => {\n                        this.model.response_id = e.target.value || null;\n                    });\n            }\n\n            // Initialize Select2 for repair selector\n            if ($(\"#repair_select\").length) {\n                $(\"#repair_select\")\n                    .select2({\n                        placeholder: \" \",\n                        allowClear: true,\n                        width: \"100%\",\n                    })\n                    .on(\"change\", (e) => {\n                        this.model.repair_id = e.target.value || null;\n                    });\n            }\n        },\n    },\n};\n</script>\n\n<style>\ntable tr > td,\ntable tr > th {\n    vertical-align: middle;\n}\n\n.table thead th {\n    vertical-align: middle;\n    padding: 3px;\n    background: gray;\n    border: 1px solid white;\n    text-align: center;\n}\n\n.table td {\n    cursor: move;\n}\n\n.table td,\n.table th {\n    vertical-align: middle;\n    padding: 3px;\n}\n\n.table td input,\n.table th input {\n    padding: 3px;\n}\n\n.calculator {\n    color: black;\n}\n\ntable.table-striped {\n    /*border-top-color: #000; \n    border-top-width: 1; \n    border-top-style: solid;*/\n    border: 3px solid gray;\n}\n\ntable.table-striped tr.calculator {\n    background-color: #fff;\n\n    border-bottom-color: #000;\n    border-bottom-width: 1;\n    border-bottom-style: solid;\n\n    border-top-color: #000;\n    border-top-width: 1;\n    border-top-style: solid;\n}\n\ntable {\n    border-collapse: collapse;\n    width: 100%;\n}\n\n/* Select2 styling improvements */\n.select2-container {\n    width: 100% !important;\n}\n\n.select2-container .select2-selection--single {\n    height: 38px !important;\n    border-radius: 0.25rem !important;\n    border: 1px solid #ced4da !important;\n}\n\n.select2-container .select2-selection--single .select2-selection__rendered {\n    line-height: 36px !important;\n    padding-left: 12px !important;\n    color: #495057 !important;\n}\n\n.select2-container .select2-selection--single .select2-selection__arrow {\n    height: 36px !important;\n    right: 10px !important;\n}\n\n.select2-container--default\n    .select2-selection--single\n    .select2-selection__placeholder {\n    color: #6c757d !important;\n}\n\n.select2-dropdown {\n    border-radius: 0.25rem !important;\n    border: 1px solid #ced4da !important;\n}\n</style>\n\n<!-- this.model.category_attributes.map(res => \n  ({pivot: res.pivot, ...this.model.special.find(s => s.category_attribute_id === res.id).json } || res)\n); -->\n"],"sourceRoot":""}]);
